import React, { Component } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import { connect } from "react-redux";
import { Online, Offline } from "react-detect-offline";
import GuestRoute from "./auth/GuestRoute";
import AuthRoute from "./auth/AuthRoute";
import SignIn from "./auth/SignIn";
import Home from "./Home";
import Default from "./Default";
import "./App.css";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	openImage = () => {
		console.log("clicked");
		const MozActivity = window.MozActivity;
		var pick = new MozActivity({
			name: "pick",
			data: {
				type: ["image/png", "image/jpg", "image/jpeg"],
				// In FxOS 1.3 and before the user is allowed to crop the
				// image by default, but this can cause out-of-memory issues
				// so we explicitly disable it.
				nocrop: true, // don't allow the user to crop the image
			},
		})
			.then((res) => {
				console.log(res);
				const src = window.URL.createObjectURL(res.blob);
				this.setState({ show: true, msg: src });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	render() {
		return (
			<>
				<Online>
					<BrowserRouter basename="/jazzdrive-kaios">
						<Switch>
							<GuestRoute
								path="/signin"
								exact
								component={SignIn}
							/>
							<AuthRoute path="/" exact component={Home} />
							<Route component={Default} />
						</Switch>
					</BrowserRouter>
				</Online>
				<Offline>
					<div style={{ textAlign: "center" }}>
						<h4 style={{ fontWeight: "bold" }}>
							No Internet Connection
						</h4>
						<p>Use Wi-Fi or mobile data to access the internet.</p>
						<button className="btn-red">Settings</button>
					</div>
				</Offline>
			</>
		);
	}
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(App);
