import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
const InputMsisdn = lazy(() => import("../partials/InputMsisdn"));

export class SignIn extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isLoading: false,
			msisdn: "",
			msisdnError: false,
		};
	}
	componentDidMount() {
		window.addEventListener("keydown", this.handleKeydown);
	}

	componentWillUnmount() {
		window.removeEventListener("keydown", this.handleKeydown);
	}

	handleKeydown = (event) => {
		console.log(event);
		if (event.key === "SoftRight") {
			this.onSubmitBtn.focus();
		} else if (event.key === "ArrowDown") {
			this.onSubmitBtn.focus();
		}
	};

	handleMsisdn = (status, msisdn) => {
		this.setState({
			msisdnError: status,
			msisdn: msisdn,
		});
	};

	onSubmitToLogin = (e) => {
		e.preventDefault();
		console.log("submitted");
		console.log(this.state.msisdn);
	};

	render() {
		return (
			<div className="container min-height-100">
				<div className="min-height-100 row justify-content-center align-items-center">
					<div className="col-4">
						<h3 style={{ fontWeight: "bold" }}>Sign In Screen</h3>

						<form onSubmit={this.onSubmitToLogin}>
							<div className="form-row">
								<Suspense fallback={<div></div>}>
									<InputMsisdn
										onChange={this.handleMsisdn}
										isRequired={true}
										label="Please Enter Jazz Number"
										placeholder="03xx-xxxxxxx"
										isLabel={true}
										defaultValue=""
									/>
								</Suspense>
							</div>
							<button
								type="submit"
								className="btn-red"
								ref={(btn) => (this.onSubmitBtn = btn)}
							>
								Login
							</button>
						</form>
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps)(SignIn);
